// Colors
$cyan: #66e2dc;
$tomato: #fa7453;
$mustard: #ffb964;
$white: #fcfaf9;
$black: #191826;

// Typography

.mobile-heading {
  font-size: 40px;
  font-weight: 700;
  letter-spacing: -0.45px;
  line-height: 48px;

  color: $black;
}

.mobile-heading-s {
  font-size: 32px;
  font-weight: 700;
  line-height: 40px;

  color: $white;
}

.mobile-body {
  font-size: 18px;
  font-weight: 700;
  line-height: 28px;

  color: $white;
}

.mobile-body-s {
  font-size: 16px;
  font-weight: 700;
  line-height: 26px;

  color: $black;
}
