@import './utils.scss';

.demo-container {
  display: flex;
  padding-top: 98px;

  .demo-card {
    display: flex;
    justify-content: center;

    border-radius: 12px;

    width: 100%;

    background-color: $black;

    .illustration {
      position: absolute;
      top: 524px;

      width: 209px;
    }

    .background {
      width: 280px;
      height: 420px;
    }
  }

  .pricing-card {
    align-self: flex-end;
    position: absolute;

    border-radius: 12px;
    padding: 48px 36px;
    height: 546px;
    width: 100%;

    background-color: $tomato;

    .pricing-heading {
      margin-bottom: 12px;
    }

    .pricing-value {
      display: flex;
      align-items: center;

      margin: 8px 0 32px 0;
      height: 53px;

      font-size: 18px;

      color: $white;

      .pricing-span {
        margin-right: 15px;

        font-size: 65px;
      }
    }

    .ios-button {
      display: flex;
      justify-content: center;
      align-items: center;

      width: 296px;
      height: 61px;
      border-radius: 12px;
      border: none;
      margin-bottom: 16px;

      font-size: 18px;
      font-weight: 700;
      word-spacing: -0.18px;

      color: $white;
      background-color: $black;
    }

    .android-button {
      display: flex;
      justify-content: center;
      align-items: center;

      width: 296px;
      height: 61px;
      border-radius: 12px;
      border: none;

      font-size: 18px;
      font-weight: 700;
      word-spacing: -0.18px;

      color: $black;
      background-color: $white;
    }

    .icon {
      margin-right: 8px;
    }
  }
}
